@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@500;600;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
    margin: 0;
    padding: 0;
    font-family: 'Montserrat', sans-serif;
}
.header {
    background: #4F27BF;
    position: fixed;
    width: 100%;
    z-index: 10;
}
.container {
    height: 80px;
    display: flex;
    justify-content: space-between;
    margin: 0 36px;
    align-items: center;
}
.logo__box {
    display: flex;
    align-items: center;
}
.logo__img {
    width: 48px;
    height: 48px;
}
.logo__title {
    font-weight: bold;
    font-size: 20px;
    line-height: 24px;
    color: #FFFFFF;
    margin-left: 24px;
}
.user__box {
    display: flex;
    align-items: center;
}
.user__img {
    width: 48px;
    height: 48px;
}
.user__title {
    font-weight: bold;
    font-size: 20px;
    line-height: 24px;
    color: #FFFFFF;
    margin-left: 16px;
}



@media(max-width: 425px) {
    .container {
        margin: 0 15px;
    }
    .logo__img {
        width: 38px;
        height: 38px;
    }
    .logo__title {
        font-size: 15px;
    }
    .user__img {
        width: 38px;
        height: 38px;
    }
    .user__title {
        font-size: 15px; 
    }
}
.home {
    padding: 112px 32px 32px 332px;
}
.title {
    font-weight: bold;
    font-size: 26px;
    line-height: 32px;
    color: #1C1C1E;
}
.subtitle {
    font-weight: 500;
    font-size: 20px;
    line-height: 24px;
    color: #1C1C1E;
    margin: 16px 0 24px 0;
}
.content {
    margin-top: 16px;
    font-weight: 500;
    font-size: 20px;
    line-height: 24px;
    color: #000000;
}



@media(max-width: 768px) {
    .home {
        padding: 112px 32px 32px 32px;
    }
}
@media(max-width: 425px) {
    .home {
        padding: 112px 15px 32px 15px;
    }
    .content {
        font-size: 15px;
        line-height: 24px;
    }
}
.search {
    padding: 112px 32px 32px 332px;
}
.title {
    font-weight: bold;
    font-size: 26px;
    line-height: 32px;
    color: #1C1C1E;
}
.subtitle {
    font-weight: 500;
    font-size: 20px;
    line-height: 24px;
    color: #1C1C1E;
    margin: 16px 0 24px 0;
}
form {
    display: flex;
}
input {
    width: 100%;
    height: 70px;
    border: 3px solid #4F27BF;
    box-sizing: border-box;
    border-radius: 10px;
    font-weight: 500;
    font-size: 20px;
    line-height: 24px;
    color: #6C6C6C;
    padding-left: 24px;
}
button {
    min-width: 217px;
    height: 70px;
    background: #4F27BF;
    border-radius: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 24px;
}
img {
    width: 32px;
}
span {
    font-weight: bold;
    font-size: 26px;
    line-height: 32px;
    color: #FFFFFF;
    margin-left: 16px;
}
.addresses__box {
    background: #FFFFFF;
    box-shadow: 0px 2px 8px rgba(2, 0, 97, 0.25);
    border-radius: 10px;
    width: 100%;
    height: 100%;
    margin: 32px 0;
}
.addresses__title {
    font-weight: bold;
    font-size: 26px;
    line-height: 32px;
    color: #000000;
    padding: 24px 0 16px 24px;
}
.line {
    width: 100%;
    border: 1px solid #D7D7D7;
    margin: 16px 0;
}
.item {
    font-weight: 500;
    font-size: 20px;
    line-height: 24px;
    color: #000000;
    margin: 0 24px;
    list-style-type: none;
}
.item:last-child {
    padding-bottom: 24px;
}



@media(max-width: 768px) {
    .search {
        padding: 112px 32px 32px 32px;
    }
}
@media(max-width: 1024px) {
    form {
        display: block;
    }
    button {
        width: 100%;
        margin-left: 0;
        margin-top: 24px;
    }
}
@media(max-width: 425px) {
    .search {
        padding: 112px 15px 32px 15px;
    }
    .subtitle {
        font-size: 15px;
    }
    input {
        height: 50px;
        font-size: 15px;
        padding-left: 15px;
    }
    button {
        height: 50px;
    }
    img {
        width: 20px;
    }
    span {
        font-size: 15px;
        margin-left: 16px;
    }
    .item {
        font-size: 15px;
        margin: 0 15px;
    }
}
.menu {
    width: 300px;
    box-shadow: 0px 0px 5px rgba(2, 0, 96, 0.25);
    height: 100vh;
    position: fixed;
    padding-top: 80px;
}
.menu__title {
    font-weight: bold;
    font-size: 20px;
    line-height: 24px;
    color: #1C1C1E;
    padding: 24px 0 0 32px;
}
.menu__cell {
    margin: 40px 0 0 32px;
    display: flex;
    align-items: center;
}
.menu__img {
    width: 32px;
}
.menu__a {
    margin-left: 16px;
    font-weight: 600;
    font-size: 20px;
    line-height: 24px;
    color: #1C1C1E;
    text-decoration: none;
}
.triangle {
    width: 15px;
    margin-left: 67px;
    -webkit-transform: rotate(60deg);
            transform: rotate(60deg);
}
.triangle-active {
    width: 15px;
    margin-left: 67px;
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
.menu__cell_min {
    margin: 32px 0 32px 80px;
    display: flex;
    align-items: center;
}
.close {
    display: none;
}



@media(max-width: 768px) {
    .menu {
        display: none;
    }
}
